import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import { api } from '@/config/common.js'
import { createProductUnit } from '@/api/product.js'
import getGlobalData from '@/utils/business.js'
export default {
	components: {
		uniSearchBar,
		cuPanel,
		cuCell,
		uniList,
		uniListItem,
		uniPopup
	},
	data() {
		return {
			name: '',
			title: '计量单位',
			datas: [],
			searchDatas: [],
			reqData: {
				unit: ''
			}
		}
	},
	onLoad(options) {
		this.name = options.name
	},
	onShow() {
		this.datas = uni.getStorageSync('productCategory').units
		this.searchDatas = this.datas
	},
	computed: {
		headerHeight() {
			return this.$headerIsSearchHeight
		},
		mainHeight() {
			return this.$mainIsSearchHeight
		}
	},
	methods: {
		handleClickItem(val) {
			let pages = getCurrentPages()
			let prevPage = pages[pages.length - 2]
			if (this.name == 'unit') {
				prevPage.setData(
					{ 
						rData: { key: 'unit', value: val.unit}
					}
				)
			} else {
				prevPage.setData(
					{ 
						rData: { key: 'subUnit', value: val.unit}
					}
				)
			}
			
			uni.navigateBack({
				delta: 1
			})
		},
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleSearch(val) {
			if (val.value) {
				this.searchDatas = this.datas.filter((item) => {
					if (!item.unit) {
						item.unit = ''
					}
					return item.unit.indexOf(val.value) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		},
		handleShowPopup() {
			this.reqData = {
				unit: '',
				status: 1
			}
			this.$refs.popup.open()
		},
		handleStatusChange(val) {
			this.reqData.status = val.detail.value
		},
		handleAdd() {
			if (this.reqData.unit) {
				createProductUnit(api.baseProduct, this.reqData).then(res => {
					this.$refs.popup.close()
					if (res.status == 200 && res.data.returnCode == '0000') {
						getGlobalData.getProductCategory().then(res => {
							this.datas = uni.getStorageSync('productCategory').units
							this.searchDatas = this.datas
						})
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.returnMessage
						})
					}
				}).catch(error => {
					uni.showToast({
						icon: 'none',
						title: error
					})
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请输入计量单位名称'
				})
			}
		},
		handleCancel() {
			this.$refs.popup.close()
		}
	}
}
